$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  .title {
    color: $color-default;
    font-size: $font-size-bigger;
    font-weight: 700;
  }
  .menu,
  .search {
    text-align: center;
    border-radius: 50%;
    color: $color-light-gray;
    i {
      font-size: $font-size-normal;
    }
  }
}
