$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

.toast {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  max-width: 60%;
  width: fit-content;
  z-index: 2000;
  &:global(.drop-enter),
  &:global(.drop-appear) {
    bottom: 0;
  }
  &:global(.drop-enter-active),
  &:global(.drop-appear-active) {
    bottom: 20px;
    transition: all 0.2s;
  }
  &:global(.drop-exit) {
    bottom: 20px;
  }
  &:global(.drop-exit-active) {
    bottom: 0;
    transition: all 0.2s;
  }
}
