$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

.nav-tabs {
  justify-content: space-between;
  width: 100%;
  padding: 15px 0;
  a {
    flex: 1;
    padding: 0 20px 0 0;
    font-size: 1.25rem;
    color: $color-gray;

    text-align: center;
    &.actived {
      color: $color-primary-light;
    }
    &:hover {
      text-decoration: none;
    }
  }
}
